import React from 'react';
import './App.css';
import { Route, Switch, NavLink } from 'react-router-dom';

import TabsPage from './TabsPage';

const Home = () => <h1>Home</h1>;
const App = () => (
  <div className="App">
    <nav>
      <ul className="navigation">
        <li>
          <NavLink
            to="/"
            exact
            className="navigation__link"
          >
            Home
          </NavLink>
        </li>

        <li>
          <NavLink
            to="/tabs"
            className="navigation__link"
          >
            Tabs
          </NavLink>
        </li>
      </ul>
    </nav>

    <Switch>
      <Route
        path="/"
        exact
        component={Home}
      />

      <Route
        path="/tabs/:tabId?"
        exact
        component={TabsPage}
      />
    </Switch>
  </div>
);

export default App;
